import request from '@/utils/request'
//  - 
export function getVersusUserItemActivity (params) {
  return request({
    url: '/versus/user/item_activity',
    method: 'get',
    params
  })
}
//  -  - 
export function getVersusUserJoinScore (id) {
  return request({
    url: '/versus/user/join/score/' + id,
    method: 'get'
  })
}
